.Square{
    width: 60px;
    height: 60px;
    border-radius: 4px;
    background: rgb(255, 104, 104);
    color: white;
    font-size: 30px;
    border: 3px solid orange;
    margin: 2px;
    box-sizing: border-box;
    transition: all 0.3s ease;
}
.Square:hover{
    background: rgb(253, 51, 51);
    cursor: pointer;
}
.Highlight{
    background: orange;
}
.Highlight:hover{
    background: orange;
}